main {
    // background-color: black;
    font-family: 'Lato', sans-serif;
    overflow-x: hidden;
    padding-top: 40px;
}
.color {
    &-white {
        color: blanchedalmond !important;
    }
}
hr {
    background: #131313;
}
section {
    background-color: black;
    padding: 32px;
}
.laser {
    background-color: black;
    padding: 32px;
    h1 {
        color: whitesmoke;
        font-family: 'Ubuntu', sans-serif;
    }
    p {
        color: #fafafa;
    }
    .wrap-800 {
        width: 800px;
        margin: 0 auto;
        @include breakpoint (mobile) {
            width: auto;
        }
    }
    .img {
        width: 100%;
        height: 100%;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    .title {
        vertical-align: middle;
        font-size: 42px;
        color: blanchedalmond;
        font-family: 'Ubuntu', sans-serif;
    }
    .line {
        width: 90px;
        border: 1px solid blanchedalmond;
        margin: 16px 0;
    }
    .client {
        .logo {
            padding: 24px;
            // background: radial-gradient(circle, #131313 64%,#000 90%);
            display: block;
            width: 980px;
            text-align: center;
            margin: 0 auto 72px;
            @include breakpoint (mobile){
                width: 320px;
                overflow-x: auto;
                margin: 24px auto 0;
                white-space: nowrap;
            }
            &__brand {
                display: inline-block;
                margin: 8px 16px;
            }
        }
    }
    .products {
        padding: 24px 0;
    }
    .usp {
        padding: 80px 0;
        @include breakpoint (mobile) {
            padding: 24px 0;
        }
        &__sub {
            opacity: .6;
            font-size: 24px;
        }
    }
}

.new_card {
    border: 4px solid #222;
    padding: 8px;
    border-radius: 25px;
    // box-shadow: 2px 1px 10px 2px #999;
    // background-color: blanchedalmond;
    img {
        border-radius: 20px;
    }
}


.tertarik {
    font-size: 18px;
}
.btn-light {
    margin: 8px 0;
    background-color: black;
    padding: 8px;
    font-family: 'Ubuntu',sans-serif;
    border-radius: 25px;
    color: blanchedalmond;
    width: 280px;
    font-size: 26px;
}

.moving{
    padding: 48px 0;
    position: relative;
    height: 640px;
    z-index: 2;
    &__word {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        z-index: 3;
        padding: 50px;
        border: 1px solid blanchedalmond;
        @include breakpoint (mobile) {
            padding: 16px;
            left: 10%;
            right: 10%;
            transform: translate(0%,-50%);
        }
        h1 {
            font-size: 46px;
            font-family: 'Ubuntu', sans-serif;
            @include breakpoint (mobile){
                font-size: 28px;
            }
        }
        p {
            font-size: 18px;
        }
        h1,h3,p {
            z-index: 3;
            padding: 16px 0;
            color: white;
        }
    }
    .layer {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        z-index: 2;
        opacity: .7;
        background-color: black;
        bottom: 0;
    }
    .bg-kantor {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        z-index: 1;
        bottom: 0;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.slick-dots li button:before,
.slick-dots li.slick-active button:before {
    color: #fff;
    font-size: 39px;
    content: '';
    width: 20px;
    height: 3px;
    background-color: white;
}

.slick-next:before, .slick-prev:before {
    font-family: 'fontAwesome';
}

.slick-prev:before {
    content: "\f053";
}

.slick-next:before {
    content: "\f054";
}

.slick-slide {
    margin: 0 8px;
  }
  /* the parent */
  .slick-list {
    margin: 0 -8px;
  }

.hero, .img, .new_card {
    &:focus {
        outline: 0;
    }
}

.navbar-light .navbar-toggler {
    color: blanchedalmond;
    &:focus {
        outline: 0;
    }
}

.active {
    border-bottom: 2px solid #999;
}

.kontak {
    &__title {
        font-size: 20px;
        color: blanchedalmond;
        font-family: 'Ubuntu', sans-serif;
    }
    p {
        color: whitesmoke;
    }
}

a {
    .fa {
        font-size: 28px;
        margin-right: 16px;
        width: 36px;
        margin: 4px 0
    }
}