.header {
  background-color: black;
  padding: 8px;
  .navbar {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 999;
    left: 0;
    background-color: black;
    height: 80px;
  }
  .navbar-nav {
    li {
      a{
        color: blanchedalmond !important;
        font-family: 'Ubuntu',sans-serif;
        transition: 0.2s;
        &:hover {
          opacity: .5;
        }
      }
    }
  }
  &__menu {
    ul {
      display: block;
      li {
        display: inline-block;
        list-style-type: none;
        padding: 0 16px;
        a {

        }
      }
    }
  }
}